    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
header a{
  color: #fafafa;
  text-decoration: none;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  opacity: .8;
  -webkit-transition: .3s;
  transition: .3s;
}
header a:hover{
  opacity: 1;
}
header a:hover ~ header .start__icon{
  margin-right: 25px;
}
header a .start__icon{
  font-size: 36px;
  margin-right: 15px;
}
header a .start__text{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 26px;
}
.fade-enter-active, .fade-leave-active{
  /* transition-property: opacity;
  transition-duration: .4s; */
  -webkit-transition: .3s;
  transition: .3s;
}
.fade-enter-active{
  -webkit-transition-delay: .25s;
          transition-delay: .25s;
}
.fade-enter, .fade-leave-active{
  opacity: 0;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}
.start__play{
  display: block;
  border: 1px solid #fafafa;
  padding: 6px 24px;
  font-size: 22px;
  font-weight: 300;
  margin-top: 15px;
  cursor: pointer;
  -webkit-transition: .4s;
  transition: .4s;
  color: #fafafa;
  text-decoration: none;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: 8px;
  margin-right: 8px;
}
.start__play::-moz-selection{
  background: transparent;
}
.start__play::selection{
  background: transparent;
}
.start__play:hover{
  background: #fafafa;
  color: rgb(44, 44, 44);
}
.info{
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: 1px solid white;
  line-height: 30px;
  text-align: center;
  cursor: pointer;
  color: white;
  opacity: .7;
  -webkit-transition: .4s;
  transition: .4s;
}
.info:hover{
  opacity: 1;
}

